import React from 'react';

const Delete = (props) => {
        return (
          <div>
          <button className="add"
          id = { props.hover === 'yes' ? ('yes') : ('no') }
          onClick= { () => props.delete(props.index, props.id, props.number) }>Delete</button>
</div>
  )
}

export default Delete;
