import React from 'react';
import Label from './Column/Label.jsx';
import Add from './Column/Add.jsx';
import Delete from './Column/Delete.jsx';

export default class Column extends React.Component {

state = {
hover: 'no',
 }


displaybutton() {

if (this.state.hover === 'no') {
this.setState(
  {hover: 'yes'}
)
} else {
  this.setState(
    {hover: 'no'
  }
  )
}

this.props.setActiveColumn(this.props.numero);

}

addMood = (e, number) => {

this.props.addMood(e, number);

console.log(this.props.keyarray)

}

checkArray = (item) => {

this.props.keyarray.includes(item);
console.log(this.props.keyarray.includes(item));


}

checkcheck = () => {

console.log(this.props.number)

}

  render() {

    return (
    <div>

    <div className = "column" onClick = { this.checkcheck }
    onMouseEnter = { this.displaybutton.bind(this) }
    onMouseLeave = { this.displaybutton.bind(this) }
    id = {this.state.hover === 'yes' ? ("column-hover") : (null)}>

    <div id = { this.props.keyarray.includes(this.props.id) === true ? ('yes') : ('no') }>

    </div>

    <Label label = {this.props.label} />

    <div id = { this.props.keyarray.includes(this.props.id) === false ? ('yes') : ('yes') } >

    <Add hover = {this.state.hover}
          addMood = {this.addMood}
          id = {this.props.id}
          number = {this.props.number} />

    </div>

    <div id = { this.props.keyarray.includes(this.props.id) === true ? ('no') : ('no') } >

    <Delete hover = {this.state.hover}
    delete = {this.props.delete}
    index = {this.props.index}
    id = {this.props.id}
    number = {this.props.number}
    markers = { this.props.markers }
    numero2 = { this.props.numero2 }
    />

    </div>

    </div>

    </div>
  )
}
}
