import React from 'react';
import Toggle from './Toggle.jsx';
import Moodbuttons from './Moodbuttons.jsx';
import Moodlabels from './Moodlabels.jsx';
import Notes from './Notes.jsx';
import Submit from './Submit.jsx';
import Header from './Header.jsx';
import AddFood from './AddFood.jsx';

export default class Popup extends React.Component {

state = {
  colour: "mood-4",
  show: "no",
  text: '',
  columnkey: '',
  toggle: 'mood',
  calibrate: '0'
}

  changeMood = (e) => {
    this.setState({
    colour: e
    })
  };

  closePopup() {
    this.props.close("no");
  }

  handleChange = (e) => {
    this.setState({
    text: e.target.value
    })
    this.props.textChange(this.state.text);

  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({
    columnkey: this.props.columnkey})
    this.props.addMarker(this.props.columnkey, this.state.colour);
    this.closePopup();
  }

  handleSubmitFood = (e) => {
    e.preventDefault();
    // let notes = textarea.value
    this.props.addFood(this.state);
    this.closePopup()
  }

  toggleFood = () => {
    this.setState({
    toggle: 'food'})
  }

  toggleMood = () => {
    this.setState({
    toggle: 'mood'})
  }

  calibrate = () => {
    if (this.props.keyarray.includes(this.props.columnkey) === true) {
      this.setState({
      toggle: 'food'})
    }
  }

  render() {
    return (
    <div className="modal" id={this.props.toggle} >


    <div className="modal-content">


    < Toggle toggleFood= {this.toggleFood}
            toggleMood= {this.toggleMood}
            toggle = {this.state.toggle}
            columnkey = {this.props.columnkey}
            keyarray = {this.props.keyarray}
            closePopup={this.closePopup.bind(this)}/>


    <div className="modalwrapper">

    <Header closePopup={this.closePopup.bind(this)}/>


    <div id = {this.props.keyarray.includes(this.props.columnkey) === true ? ('no') : ('yes') } >
    <div id = {this.state.toggle === 'mood' ? ('yes') : ('no')}>
    <form onSubmit={this.handleSubmit.bind(this)}>

    <span className="dialogue">How do you feel right now?</span>

    <Moodbuttons changeMood = {this.changeMood}
    colour = {this.state.colour}/>

    <Moodlabels />

    <Notes handleChange = {this.handleChange} />

    <Submit />
    </form>
    </div>
    </div>


    <div id = {this.state.toggle === 'food' || this.props.keyarray.includes(this.props.columnkey) === true ? ('yes') : ('no')}>

    <AddFood handleSubmitFood = {this.handleSubmitFood.bind(this)}
            handleChange = {this.handleChange}
            />
    </div>
    </div>






</div>
    </div>
  )
}
}
