import React from 'react';

const Moodmarkers = (props) => {

      return (
        props.markers[props.index] === undefined ? (null) : (props.markers[props.index].map(markers => {
        return (
        <div className={ markers.number } id={ markers.colour } key={ markers.id }
        onClick = { () => {props.revealnote(props.index, markers.id)}} >
        </div>
        )
      }
)
)
)
}

export default Moodmarkers;
