import React from 'react';
import CloseButton from './CloseButton.jsx';

const Toggle = (props) => {
        return (
          <div className="toggle">
          <div className= {props.toggle === 'mood' ? ("togglebutton") : ("inactivetoggle")} id = {props.keyarray.includes(props.columnkey) === true ? ('inactivetoggle') : ('yes')} onClick= {props.toggleMood} >
          <span className = "togglelabel">Add Mood</span>
          </div>

          <div className= {props.toggle === 'food' || props.keyarray.includes(props.columnkey) === true ? ("togglebutton") : ("inactivetoggle")} onClick= {props.toggleFood} >
          <span className = "togglelabel">Add Food</span>
          </div>

          <CloseButton closePopup = {props.closePopup} />

          </div>
  )
}

export default Toggle;
