import React from 'react';

const Foodmarkers = (props) => {
  return (
    props.food[props.index].map(food => {

    return (
    <div className="food" id = { food.number } onClick = { () => {props.revealFoodnote(props.index, food.id)}}
>
    </div>
        )
      }
)
)

}

export default Foodmarkers;
