import React from 'react';

const Notes = (props) => {
        return (
        <div className = "note" id = { props.appear } >

        <div className="bubble" onClick={ props.close }>

        <div className = "noteheader">
        <span className="minicloseBtn" onClick={ props.close }>&times;</span>
        </div>

        <div className = "notewrapper">
        <span> { props.textual } </span>
        </div>

        </div>

        <div className = "notefooter">
        <span className = "notelabel" onClick= { () => props.delete(props.deletionindex, props.deletionindex2) }> Delete Marker</span>
        </div>

        </div>
  )
}

export default Notes;
