import React from 'react';
import CloseButton from './Popup/CloseButton.jsx';

export default class Popup extends React.Component {

state = {
  show: "no",
  popupVisible: false
 }

  closePopup() {
    this.props.close(this.state.show);
  }

  handleClick() {
      if (!this.state.popupVisible) {
        // attach/remove event handler
        document.addEventListener('click', this.handleOutsideClick, false);
      } else {
        document.removeEventListener('click', this.handleOutsideClick, false);
      }

      this.setState(prevState => ({
         popupVisible: !prevState.popupVisible,
      }));
    }

  render() {

    return (

    <div className="modal" id= { this.props.toggle } >
    <div className = "modal-content">

    <div className = "popupheader">
    <div className = "closemodal" onClick= { this.closePopup.bind(this) }>
    <span className = "closeBtn">&times;</span>
    </div>

    </div>

    <div className  = "modalwrapper">

    <h1>Welcome, buddy!</h1>
    <div className = "spacer2"></div>

    <span className = "dialogue2">
    Chroma is a visual food journal. Here's a brief rundown:
    <p></p>
    <ul>
    <li>Each day has three blocks: morning, afternoon, and evening.</li>
    <li>Add food markers to each block to keep track of what you ate during that period.</li>
    <li>Assign ONE mood marker to each block to represent how you felt during that period.</li>
    <li>Click on each marker to reveal the notes you wrote.</li>
    </ul>
    </span>
    <div className = "spacer2"></div>
    <button className="enter" onClick= { this.closePopup.bind(this) } >Got it!</button>

</div>

    </div>
    </div>
  )
}
}
